import React, { useEffect, useState } from 'react';
import { Button, Form, InputGroup } from 'react-bootstrap';
import { useForm } from 'react-hook-form';
import { useParams } from 'react-router-dom';
import { Helmet } from 'react-helmet';
import { CarEditForm } from '../../../../types/Edit';
import { useCategory } from '../../../Categories/hooks';
import { imageOnErrorHandler } from '../../../../utils/helpers/imageOnErrorHandler';
import ContentContainer from '../../../../components/ContentContainer/ContentContainer';
import FormContainer from '../../../../components/FormContainer/FormContainer';
import MyProgressBar from './components/MyProgressBar/MyProgressBar';
import styles from './styles.module.scss';

const CarEdit = (): React.ReactElement => {
  const { carId } = useParams();
  const [colorValue, setColorValue] = useState<string>('');
  const [selectedCategory, setSelectedCategory] = useState<string>('');
  const [categories, categoriesLoading] = useCategory();
  const {
    register,
    handleSubmit,
    watch,
    setValue,
    getValues,
    getFieldState,
    formState: { errors },
    formState,
  } = useForm<CarEditForm>({
    defaultValues: {
      car: '',
      category: 'Выберите категорию',
      description: '',
      colorsArray: [],
      image: null,
    }
  });
  const { ref } = register('colorsArray', {
    validate: (value) => value.length > 0 || 'Добавьте хотя бы один цвет',
  });
  const currentCar = watch('car');
  const currentImage = watch('image');
  const currentCategory = watch('category');
  const colorsCheckboxes = watch('colorsArray');

  useEffect(() => {
    if (categories) {
      const currCategoryObject = categories.find((category) => category.id === currentCategory);
      setSelectedCategory(currCategoryObject?.name);
    }
  }, [currentCategory]);

  const addColor = (): void => {
    const currentColors = getValues('colorsArray');
    setValue('colorsArray', [...currentColors, colorValue], { shouldValidate: true, shouldDirty: true });
    setColorValue('');
  };

  const deleteColor = (colorForDelete): void => {
    const currentColors = getValues('colorsArray');
    setValue('colorsArray', currentColors.filter((color) => color !== colorForDelete), { shouldDirty: true });
  };

  const imageUploadHandler = (e): void => {
    const selectedImg = e.target.files[0];
    if (selectedImg) {
      setValue('image', selectedImg, { shouldDirty: true });
    }
  };

  useEffect(() => {
    console.log(currentImage);
  }, [currentImage]);

  return (
    <>
      <Helmet>
        <title>Need For Drive - Карточка автомобиля</title>
        <meta name="viewport" content="width=device-width, initial-scale=1" />
        <meta name="description" content="Home page" />
      </Helmet>
      <ContentContainer
        title="Карточка автомобиля"
        className={styles['car-edit-container-wrap']}
      >
        <Form
          noValidate
          autoComplete="off"
          onSubmit={handleSubmit((data) => {
            console.log(data);
          })}
        >
          <div className={styles['car-edit']}>
            <div className={styles['car-edit__card']}>
              <img
                src={currentImage && currentImage.length > 0 ? URL.createObjectURL(currentImage[0]) : ''}
                alt="car-name"
                onError={imageOnErrorHandler}
              />
              <div className={styles['car-edit__details']}>
                <h2>
                  {currentCar}
                </h2>
                <span>
                  {selectedCategory && selectedCategory.length > 0 ? selectedCategory : 'Категория не выбрана'}
                </span>
              </div>
              <div className={styles['car-edit__file-input-container']}>
                <Form.Control
                  type="file"
                  size="sm"
                  accept="image/png, image/jpeg"
                  className={styles['car-edit__file-input']}
                  {...register('image', {
                    required: 'Выберите изображение автомобиля',
                  })}
                  isInvalid={!!(errors.image)}
                />
                {(errors.image?.message) && (
                  <Form.Text
                    id="username"
                    bsPrefix="input-message"
                  >
                    {errors.image?.message}
                  </Form.Text>
                )}
              </div>
              <MyProgressBar
                formStates={[
                  getFieldState('car', formState),
                  getFieldState('category', formState),
                  getFieldState('colorsArray', formState),
                  getFieldState('image', formState)
                ]}
              />
              <Form.Group className={styles['car-edit__description-textarea']} controlId="carDescription">
                <Form.Label>Описание</Form.Label>
                <Form.Control
                  as="textarea"
                  rows={4}
                  aria-describedby="carDescription"
                  placeholder="Введите описание"
                  {...register('description', {
                    required: 'Это поле не должно быть пустым',
                  })}
                  isInvalid={!!(errors.description)}
                />
                {(errors.description?.message) && (
                  <Form.Text
                    id="username"
                    bsPrefix="input-message"
                  >
                    {errors.description?.message}
                  </Form.Text>
                )}
              </Form.Group>
            </div>
            <FormContainer
              title="Настройки автомобиля"
              isCreateTable={!carId}
              className={styles['car-form-container']}
            >
              <Form.Group controlId="carName">
                <Form.Label>Модель автомобиля</Form.Label>
                <div className="input-wrap">
                  <Form.Control
                    type="text"
                    placeholder="Введите модель"
                    aria-describedby="carName"
                    {...register('car', {
                      required: 'Это поле не должно быть пустым'
                    })}
                    isInvalid={!!(errors.car)}
                  />
                  {(errors.car?.message) && (
                    <Form.Text
                      id="username"
                      bsPrefix="input-message"
                    >
                      {errors.car?.message}
                    </Form.Text>
                  )}
                </div>
              </Form.Group>
              <Form.Group controlId="categoryName">
                <Form.Label>Тип автомобиля</Form.Label>
                <div className="input-wrap">
                  <Form.Select
                    size="sm"
                    disabled={categoriesLoading}
                    aria-describedby="pointCity"
                    {...register('category', {
                      required: 'Это поле не должно быть пустым',
                      validate: value => value !== 'Выберите категорию' || 'Вы не выбрали категорию',
                    })}
                    isInvalid={!!(errors.category)}
                  >
                    <option value="Выберите категорию">Выберите категорию</option>
                    {categories && categories.map((category) => (
                      <option key={category.id} value={category.id}>{category.name}</option>
                    ))}
                  </Form.Select>
                  {(errors.category?.message) && (
                    <Form.Text
                      id="username"
                      bsPrefix="input-message"
                    >
                      {errors.category?.message}
                    </Form.Text>
                  )}
                </div>
              </Form.Group>
              <Form.Group controlId="CarColors">
                <Form.Label>Доступные цвета</Form.Label>
                <div className="input-wrap">
                  <InputGroup className={styles['form__colors-input-group']}>
                    <Form.Control
                      type="text"
                      placeholder="Введите цвет"
                      aria-describedby="CarColors"
                      value={colorValue}
                      ref={ref}
                      onChange={(e) => setColorValue(e.target.value)}
                      isInvalid={!!(errors.colorsArray)}
                    />
                    <Button
                      variant="outline-primary"
                      className={styles['form__colors-add-button']}
                      onClick={addColor}
                      disabled={colorValue.length === 0}
                    >
                      Add Color
                    </Button>
                  </InputGroup>
                  {/*@ts-ignore*/}
                  {(errors.colorsArray?.message) && (
                    <Form.Text
                      id="username"
                      bsPrefix="input-message"
                    >
                      {/*@ts-ignore*/}
                      {errors.colorsArray?.message}
                    </Form.Text>
                  )}
                </div>
                <div className={styles['form__colors-checkbox']}>
                  {colorsCheckboxes.map((color, index) => (
                    <Form.Check
                      type="checkbox"
                      label={color}
                      key={`color-${index}`}
                      onClick={() => deleteColor(color)}
                      defaultChecked
                    />
                  ))}
                </div>
              </Form.Group>
            </FormContainer>
          </div>
        </Form>
      </ContentContainer>
    </>
  );
};

export default CarEdit;
